export default class SubscriptionGambit {
  pattern() {
    return 'is:(follow|ignor)(?:ing|ed)';
  }
  toFilter(matches, negate) {
    const type = matches[1] === 'follow' ? 'following' : 'ignoring';
    return {
      subscription: type
    };
  }
  filterKey() {
    return 'subscription';
  }
  fromFilter(value, negate) {
    return "".concat(negate ? '-' : '', "is:").concat(value);
  }
}
flarum.reg.add('flarum-subscriptions', 'common/query/discussions/SubscriptionGambit', SubscriptionGambit);